@use '@angular/material' as mat;

// Define the theme using Angular Material's theme system
$theme: mat.define-theme(
  (
    color: (
      theme-type: light,
      primary: mat.$azure-palette,
      tertiary: mat.$blue-palette,
    ),
    typography: (
      brand-family: 'Roboto, sans-serif',
      bold-weight: 700,
    ),
    density: (
      scale: 0,
    ),
  )
);

html {
  @include mat.all-component-themes($theme);
  @include mat.typography-hierarchy($theme);
}

// Google Fonts
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&family=Inter:wght@400;500;600&family=Newsreader:wght@400;500;600&family=JetBrains+Mono:wght@400;600;700&display=swap');
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');

// Design tokens
:root {
  --sidebar-bg: #1a1a1a;
  --sidebar-hover: #2a2a2a;
  --sidebar-text: #ffffff;
  --sidebar-text-muted: #888888;

  --accent-teal: #0d6e6e;
  --accent-orange: #e07b54;

  --text-primary: #1a1a1a;
  --text-secondary: #666666;
  --text-tertiary: #888888;
  --text-muted: #aaaaaa;

  --bg-card: #ffffff;
  --bg-page: #fafafa;
  --bg-muted: #f8f8f8;
  --bg-control: #f0f0f0;

  --border-primary: #e5e5e5;
  --border-divider: #f0f0f0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  background-color: #f5f5f5;
}

// Auth layout
.auth-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px 16px;
}

.auth-card {
  width: 100%;
  max-width: 440px;
}

.auth-card mat-card-header {
  margin-bottom: 8px;
}

.auth-card mat-card-title {
  font-size: 24px;
  font-weight: 500;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.auth-form mat-form-field {
  width: 100%;
}

.field-row {
  display: flex;
  gap: 16px;

  mat-form-field {
    flex: 1;
  }
}

.auth-actions {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 8px;
}

.auth-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  margin-top: 16px;
  font-size: 14px;
}

// Dashboard layout
.dashboard-page {
  min-height: 100vh;
  background-color: #f5f5f5;
}

.dashboard-content {
  max-width: 960px;
  margin: 0 auto;
  padding: 32px 24px;
}
